import React from 'react';
import classnames from 'classnames';
import Demo0 from '../components/Demo0';
import Demo1 from '../components/Demo1';
import styles from './index.less';

const demosList = [
  { id: 'demo0', item: <Demo0 /> },
  { id: 'demo1', item: <Demo1 /> },
];

const view = (props: any = {}) => {
  let demos: any[] = [];
  if (props?.showType === 'last') {
    demos = [demosList[demosList.length - 1]];
  } else if (props?.showType === 'first') {
    demos = [demosList[0]];
  } else {
    const item = demosList.find(i => i.id === props?.showType);
    demos = item ? [item] : demosList;
  }
  return (
    <div className={classnames(styles.list, demos.length === 1 && styles.solo)}>
      {demos.map(i => (
        <div key={i.id} className={styles.item}>
          <div className={styles.view}>
            {i.item}
          </div>
        </div>
      ))}
    </div>
  );
};

export default view;
