import React, { useState, useRef } from 'react';
import { useFrame } from '@react-three/fiber';

interface IProps {
  position?: number[];
};

export default function Box(props: IProps) {
  const ref = useRef();
  const [hovered, setHovered] = useState(false);
  // const [clicked, setClicked] = useState(false);
  const [colorIndex, setColorIndex] = useState(3);
  const colors = [
    '#f5222d', '#fa541c', '#fa8c16', '#faad14',
    '#fadb14', '#a0d911', '#52c41a', '#13c2c2',
    '#1677ff', '#2f54eb', '#722ed1', '#eb2f96'
  ];
  // 动画
  useFrame((state, delta) => ((ref.current as any).rotation.x += delta));
  return (
    <mesh
      {...props}
      ref={ref}
      // click
      onClick={() => setColorIndex(colorIndex >= colors.length - 1 ? 0 : (colorIndex + 1))}
      // hover
      onPointerOver={(event) => (event.stopPropagation(), setHovered(true))}
      onPointerOut={(event) => setHovered(false)}
      scale={hovered ? 1.5 : 1}
    >
      <boxGeometry args={[1, 1, 1]} />
      <meshStandardMaterial color={colors[colorIndex]} />
    </mesh>
  );
};
